.swipe-wrapper {
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid;
}
.swipe-able {
    display: flex;
    flex: 1;
    justify-content: space-between;
    background-color: gray;
    transition: all 0.5s;
}
.swipe-actions {
    width: 0;
    overflow: hidden;
    display: flex;
    transition: all 0.5s;
}
.action {
    width: 100%;
}
